<template>
  <div class="header">
    <div class="head-left">
      <slot name="setLeft"></slot>
      <div class="back" v-if="goBack && goBack!== 'finished'" @click.stop="$router.go(-1)">
        <img src="../../assets/global/back@3x.png" />
      </div>
      <div class="back" v-else-if="goBack === 'finished'" @click="finished">
        finished
      </div>
    </div>
    <div class="head-center">{{title}}
      <slot name="system"></slot>
    </div>
    <div class="head-right">
      <slot name="setRight"></slot>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  data () {
    return {}
  },

  props: [ 'title', 'goBack' ],

  computed: {
    ...mapState([])
  },

  methods: { }
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../../styles/mixin.less';

.header{
  .wh-l(100%, 100px);
  background: @themeColor;
  font-size: .48rem;
  color: #F5FAFF;
  position: relative;
  top: 0;
  .head-left{
    .wh-f(20%, 100%);
    .back{
      .wh(1.066667rem, 100%);
      margin-left: .2rem;
      img{
        height: .533333rem;
        vertical-align: middle;
      }
    }
  }
  .head-center{
    .wh-f(60%, 100%);
    text-align: center;
  }
  .head-right{
    .wh-f(20%, 100%);
    color: #f8b529;
  }
}

</style>



